import React, { Component } from 'react';
import MlbTableHeader from './MlbtableHeader';
import MlbTableBody from './MlbtableBody';



export default class Table extends Component {
//      Table  
//const Table = ({ columns, sortColumn, onSort, data }) => {

getSelectPlayer = (item, team, tname) =>{          
  this.props.onChange(item, team, tname);
}; 
render() { 
  const {columns, sortColumn, onSort, data, team} = this.props;
  //console.log(this.props);
  return (
    <div className="select-player">
      <MlbTableHeader        
        columns={columns}
        sortColumn={sortColumn}
        onSort={onSort}
      />
      <MlbTableBody        
        data={data}
        columns={columns}
        onChange={this.getSelectPlayer}
        team={team}
      />
    </div>
   );
  }
}
