import React from 'react';
import axios from "axios";
import ScrollArea from 'react-scrollbar';
import * as utils from '../common/utils';
import _ from 'lodash';
import Lodding from "../common/Lodding";
import "../../css/main.css";

class Prizes extends React.Component {    
  state = {  
    isLoading: true,
    prizes: [],              
    rankStart: [],
    rankEnd: [],
    rewards: [],
    convertValue: '',
    id: this.props.prop.gi,    
    gameid: this.props.prop.gi,
    gametype: this.props.prop.gt,                         
  };   

  getMeta = async (meta) =>{
    const {data : {result: prizes }} = await axios.get(utils.DATA_URL+`meta/${meta}`);                
    this.setState({
        prizes: prizes,
        rankStart: JSON.parse(prizes.rankStart),
        rankEnd: JSON.parse(prizes.rankEnd),
        rewards: JSON.parse(prizes.rewards),
        convertValue: Number(prizes.convertValue)   
    });
  }  
  componentDidMount(){    
    this.getMeta(this.props.prop.mi);    
  };
  render() {
    const {isLoading, prizes, rankStart, rankEnd, rewards, convertValue} = this.state;

    console.log(prizes);    
    return (     
      <div className="raffle-prize">
        <div className="tit">
          <p id="titName">Ranking</p>          
        </div>
        <div>
        {rankStart.map((rankstart, index) => { 
          return(
          <p key={index} >{rankstart}</p>	          
          )
        })}  

         {rewards.map((rewards, index) => { 
          console.log(rewards.value);
          return(
          <p key={index}>{rewards.value.toLocaleString()}</p>	          
          )
        })}          
        </div>
      </div>
    )
  }
}
export default Prizes;
