import React from 'react';
import ReactTooltip from 'react-tooltip';
import axios from "axios";
import ScrollArea from 'react-scrollbar';
import * as utils from '../common/utils';
import _ from 'lodash';
import Lodding from "../common/Lodding";
import "../../css/main.css";

class teamPlayer extends React.Component {    
  state = {  
    isLoading: true,
    entries: [],      
    memWin: [],      
    id: this.props.prop.gi,    
    gameid: this.props.prop.gi,
    gametype: this.props.prop.gt,    
    img :''                      
  };   

  getEntries = async (contestId) =>{
    const {data : {result: entries }} = await axios.get(utils.DATA_URL+`joined?contestId=84499`);        
    this.setState({entries: entries});
    console.log(entries);
  } 
  getWins = async (memberId) =>{
    const {data : {result: memWin }} = await axios.get(utils.DATA_URL+`wins?memberId=${memberId}`);        
    this.setState({memWin: memWin});
    //console.log(memWin);
  }; 
  componentDidMount(){
    this.getEntries();    
  };
  render() {
    const {isLoading, entries, memWin} = this.state;           
    //console.log(this.props);
    return (     
       <ScrollArea className="playlist" smoothScrolling={true} horizontal={false}>
        <ul className="entries">       
        {entries.map((enlist, index) => {                 
            return (             
              <li key={index} className="entry" data-tip data-for='global' onMouseEnter={() => this.getWins(enlist.id)}>
                  {/* <a data-tip data-for='global'> () </a>  */}
                  {utils.getImageSrc(enlist.photo)==="MQ==" ? (
                    <span><img src={`${utils.IMAGE_URL}profile/${enlist.id}`} /></span>
                  ):(
                    <span><img src={`${utils.IMAGE_URL}icon_tool_profile.png`} /></span>
                  )}                                    
                  <span>{enlist.nick}</span>                                    
                  <ReactTooltip id='global' place="bottom" type="dark" effect="solid" >
                  <div className="entries-tooltip">                                 
                    <div>
                      <h4>Number of contests won</h4>
                      <h3>
                        {/* MLB 00 Soccer 00 NFL 00 NBA 00 */}
                        SOCCER {memWin.map((cur, i) => {                          
                          return cur.gameType === 104001 ? cur.win : '';
                        }, 0)}&nbsp;
                         MLB {memWin.map((cur, i) => {                              
                          return cur.gameType === 104002 ? cur.win : '';
                        }, 0)}&nbsp; 
                         NBA {memWin.map((cur, i) => {                          
                          return cur.gameType === 104004 ? cur.win : '';
                        }, 0)}<br /> 
                         NHL {memWin.map((cur, i) => {                          
                          return cur.gameType === 104005 ? cur.win : '';
                        }, 0)}&nbsp; 
                         KBO {memWin.map((cur, i) => {                          
                          return cur.gameType === 104012 ? cur.win : '';
                        }, 0)}&nbsp;                      
                         LOL {memWin.map((cur, i) => {                          
                          return cur.gameType === 105001 ? cur.win : '';
                        }, 0)}                       
                      </h3>
                    </div>                 
                    <div className="total"><h1>Total</h1><h2>
                    {memWin.reduce((memWin, cur, i) => {
                        //console.log(memWin, cur.win, i);
                        return memWin + cur.win;
                      }, 0)}
                      </h2></div>
                  </div>                  
                  </ReactTooltip>
              </li>              
            )
        })}
        </ul>
        </ScrollArea>            
    )
  }
}
export default teamPlayer;
