import React from "react";
import update from 'react-addons-update'
import axios from "axios";
import * as utils from '../common/utils';
import { set, toUpper } from "lodash";
class Grid extends React.Component {
  state = {     
    gametype : this.props.gameType,
    contestId : this.props.contestId,
    celllist : []
  };
  getCellList = async (gametype, contestId, model) =>{             
    const {data : {result: celllist }} = await axios.get(utils.DATA_URL+`cell?gameType=${gametype}&contestId=${contestId}`);
    this.setState({      
      celllist,
      isLoading :false
    });    
  }; 
  selectCell(id){           
    this.setState({cellActive: id});      
  };
  componentDidMount() {    
    this.getCellList(this.state.gametype, this.state.contestId);                
  }
  test(){        
    var id = 5;
    var data = this.state.celllist;
    //console.log(data);
    var commentIndex = data.findIndex(function(model) {      
      //console.log(model);
      //return model.pi == null && model.ti == null && model.ct == 115001;
      return model.ct == 115001;
    });    
    //console.log(data);
    console.log(commentIndex);

    // //console.log(this.props);
     {this.state.celllist.map((cell, index) => { 
      
      cell.sel === undefined || cell.sel === '' || cell.sel === null ? (//                                    
            
            //console.log(commentIndex)                                      
            this.setState(() => {                                             
                if(index===0){
                  console.log("    ")                         
                  return {
                    celllist : update(
                      this.state.celllist, // list
                      {
                        [0]:{$merge: {
                          nu: this.props.model.nu,
                          po: this.props.model.po,
                          nm: this.props.model.nm,
                          sel: this.props.model.sel,                                    
                          id: this.props.model.id, 
                          tname: this.props.model.tname, 
                        }}
                      }              
                    )
                  };      
                }                                        
            })    
                                                       
      ) :(
        cell.id === this.props.model.id ? (  

          this.setState(() => {                                             
            if(index===0){
              console.log("  ")
              return {
                celllist : update(
                  this.state.celllist, // list
                  {
                    [0]:{$merge: {
                      nu: '',
                      po: '',
                      nm: 'Select Player',
                      sel: '',                                    
                      id: '', 
                      tname: ''
                    }}
                  }              
                )
              };      
            }                                        
          })  

        ) :(        
          console.log("  ")
        )
        
      )

     })}       

  }
  componentDidUpdate(prevProps) {       
    if (this.props.model !== prevProps.model) {                              
        this.test();       
    }
  }

  render() {        
    const {celllist, homeaway} = this.state;
    //console.log(this.state.celllist);
    console.log(this.props);

    return(    
        <div className="grid-content">
            <ul className="txt-box">
              <li>Drag your mouse to change each cells position.</li>
              <li>After arranging all the players, click Submit to participate in the Contest.</li>
            </ul>
            <div className="grid-page">
              <span className="active">1</span><span className="btn-add">+</span>
              <button className="btn btn-del" type="button">
                <span className="icon-wrapper">
                  <span className="icon-bar"></span>
                  <span className="icon-bar"></span>													
                </span>										
              </button>
            </div>
            <div className="grid-infor">              
              <p>Field No. <span id="gridCount">1/1</span></p>	
              <p>Total Entry Fee <span id="entryFee">0</span></p>              
            </div>

            <div className="cell-content">
              <div  className="cell-box">
              {celllist.map((cell, index) => {                                
                return (
                  <React.Fragment key={index}>                     
                      {index === 4 ? ( // 
                        <div teamplayer={"team"} className={this.props.selectTeam === undefined ? "cell team" : "cell team active" || this.props.homeAway === "home" ? "cell team "+this.props.homeAway+" active" : "cell team "+this.props.homeAway+" active" } onClick={() => this.selectCell(cell.co)}>
                          {this.props.selectTeam===undefined ? (
                            <div><p className="player-name">Select Team</p></div>
                          ):(
                            <div>
                              <p className="player-name">Pitcher</p>                   
                              <div className="player-photo">
                                <span className="tm">{this.props.homeAway === "home" ? this.props.selectTeam.homeabbr : this.props.selectTeam.awayabbr }</span>
                                <span className={this.props.homeAway === "home" ? "t-logo logo-KBO-"+this.props.selectTeam.homeabbr : "t-logo logo-KBO-"+this.props.selectTeam.awayabbr}></span>
                              </div>                                             
                            </div>
                          )}                          
                        </div>
                      ):(
                        <React.Fragment key={index}>                             
                        {cell.sel === undefined ? (//
                          <div id={cell.co} teamplayer={"player"} key={index} className={this.state.cellActive === cell.co ? "cell ing" : "cell"} onClick={() => this.selectCell(cell.co)}>
                            <p className="player-name">                            
                              Select Player
                            </p>                   
                            <div className="player-photo">
                              <span className="tm"></span>
                              <span className="num"></span>
                              <span className="pos"></span>
                            </div>
                            <button className="btn btn-add-del" type="button">
                              <span className="icon-wrapper">
                                <span className="icon-bar"></span>
                                <span className="icon-bar"></span>
                              </span>
                            </button>                 
                          </div>
                        ):( // 
                          
                          <div  id={cell.co} 
                                teamplayer={"player"} 
                                key={index} 
                                className={
                                //this.state.cellActive === cell.co ? "cell active ing "+this.props.model.team: "cell" &&
                                cell.sel === true ? "cell active "+this.props.model.team: "cell"
                                }
                                onClick={() => this.selectCell(cell.co)}>                                
                            <p className="player-name">                            
                              {cell.nm}
                            </p>                   
                            <div className="player-photo">
                              <span className="tm">{cell.tname}</span>
                              <span className="num">{cell.nu}</span>
                              <span className="pos">{cell.po}</span>
                            </div>
                            <button className="btn btn-add-del" type="button">
                              <span className="icon-wrapper">
                                <span className="icon-bar"></span>
                                <span className="icon-bar"></span>
                              </span>
                            </button>                 
                          </div>   

                        )}  
                        </React.Fragment>  
                      )}
                    </React.Fragment>                              
                  )
              })}
              </div>                                           
            </div>

        </div>
    )
  }
};
export default Grid;