import React from "react";
import update from 'react-addons-update'
import axios from "axios";
import * as utils from '../common/utils';
import Lodding from "../common/Lodding";
import Top from "../common/Top";
import Footer from "../common/Footer";
import SubTitle from "../common/SubTitle";
import SettingTop from "./SettingTop";
import MlbCondition from "./mlb/Condition";
import Grid from "./Grid";

class Setting extends React.Component {  
  state = {                   
    isLoading: true,    
    subTitle : "Setup",     
    contestId : this.props.match.params.id,
    item :[]
  };  
  getContestInfor = async (gametype, gameid) =>{             
    const {data : {result: settinginfo }} = await axios.get(utils.DATA_URL+`/game/${gametype}/${gameid}`);                                     
    this.setState({      
      settinginfo,
      isLoading :false      
    });           
  };
  componentWillMount(){    
    const setTopData = this.props.location.props;
    if(setTopData ===undefined){      
      this.setState({
        setTopData:  JSON.parse(localStorage.setTopData)
      })
    }else{
      this.setState({
        setTopData: setTopData
      })
      localStorage.setTopData = JSON.stringify(setTopData);  
    }           
  } 
  
  componentDidMount(){
    this.getContestInfor(localStorage.getItem('gametype'), this.state.setTopData.gi);        
  };
  selectTeam = (homeAway, state) => {            
    this.setState({
      homeAway: homeAway,
      selectTeam: state
    })   
  }         
  selectPlayer = (model, sel, team, tname) => {                
    const playerModel = update(model, {$merge:{sel:sel, team:team, tname: tname}});
    this.setState({
      model: playerModel    
    })   
  }         
  render() {            
    const {isLoading, subTitle, settinginfo, contestId} = this.state;              
    var local_gametype = localStorage.getItem('gametype');  
    
    return (
        <div className="container">
            <Top />
            {isLoading ? (
                <Lodding />
            ) : (
                [
                    <SubTitle key={1} gameType={local_gametype} subTitle={subTitle} />,                    
                    <SettingTop key={2} prop={this.state.setTopData} gameType={local_gametype} />,
                    <div key={3} className={"create-content "+utils.getAbbr(Number(local_gametype))}>                        
                        <MlbCondition gameType={local_gametype} settinginfo={settinginfo} selectTeam={this.selectTeam} selectPlayer={this.selectPlayer}/>
                        <Grid 
                          gameType={local_gametype} 
                          contestId={contestId} 
                          settinginfo={settinginfo} 
                          selectTeam={this.state.selectTeam} 
                          homeAway={this.state.homeAway}
                          model={this.state.model}
                          sel={this.state.sel}

                        />
                    </div>,                    
                    <div key={4} className="btn-box-setting">
                        <button className="rb-btn btn-back">Back</button>
                        <button className="rb-btn home-logo">go game list</button>
                    </div>                   
                ]
            )}
            <Footer />
        </div>
    )
  }
};
export default Setting;