import React from 'react';
import axios from "axios";
import ScrollArea from 'react-scrollbar';
import * as utils from '../common/utils';
import Lodding from "../common/Lodding";
import "../../css/main.css";

class teamPlayer extends React.Component {    
  state = {  
    isLoading: true,
    matchs: [],  
    rankStat :[],
    id: this.props.prop.gi,    
    gameid: this.props.prop.gi,
    gametype: this.props.prop.gt,
    homeabbr: this.props.prop.hta,
    awayabbr: this.props.prop.ata,
    hometeam: this.props.prop.htn,
    awayteam: this.props.prop.atn, 
    homeid: this.props.prop.hti,
    awayid: this.props.prop.ati, 
    gp: this.props.prop.gp, 
    cc: this.props.prop.cc,   
    ln: this.props.prop.ln,
    hs: this.props.prop.hs,
    as: this.props.prop.as,
    st: this.props.prop.st                         
  };   

  getTeamPlayer = async (gametype, gameid) =>{   
    const {data : {result: gameData }} = await axios.get(utils.DATA_URL+`game/${gametype}/${gameid}`);               
    const {data : {result: rankStat }} = await axios.get(utils.DATA_URL+`stat/team/${gametype}/${gameid}`);    
    this.setState({gameData: gameData , rankStat: rankStat, isLoading :false});

    console.log(gameData);
    console.log(rankStat);
  }
  componentDidMount(){
    this.getTeamPlayer(this.props.prop.gt, this.props.prop.gi);    
  };
  render() {
    const {isLoading, gameData, rankStat, id, submenu, gameid, gametype, homeabbr, awayabbr, hometeam, awayteam, homeid, awayid, gp, cc, ln, hs, as, st} = this.state;       
    
    console.log(this.props);
    return (
      <React.Fragment> 
        {isLoading ? (
            <Lodding />
          ) : (     
            <React.Fragment> 
            <div className="modal-team-box">
              <div className="team-first">			
                <div className="nm">              
                  <em className={"t-logo logo-"+utils.getGame(gametype)+"-"+awayabbr}></em>               
                  <span>{awayabbr}</span>          
                </div>
                <span className="hmaw">AWAY</span>
              </div>
              <div className="team-second">
                <span className="hmaw">HOME</span>
                <div className="nm">				                
                  <span>{homeabbr}</span>                
                  <em className={"t-logo logo-"+utils.getGame(gametype)+"-"+homeabbr}></em>               
                </div>			
              </div>
            </div>

            <p className="sub-tb-tit">Pitching</p>
            <div id="pit_stat" className="tp-content">
              <div>
                <div className="tp-tit-pit">
                  <span>W</span>
                  <span>L</span>
                  <span>S</span>
                  <span>ERA</span>
                  <span>SO</span>
                  <span>FPPG</span>
                </div>
                <ul className="tp-tbl-pit">
                {rankStat.stat.map((stat, index) => {                   
                  if(index===1){
                    return (                                       
                      <li key={index}>
                        <span>{stat.win}</span>
                        <span>{stat.loss}</span>
                        <span>{stat.pitSv}</span>
                        <span>{stat.pitEra}</span>
                        <span>{stat.pitSo}</span>
                        <span>-</span>                      
                      </li>                                                             
                  )}                 
                })}                 
                </ul>
              </div>
              <div>
                <div className="tp-tit-pit">
                  <span>W</span>
                  <span>L</span>
                  <span>S</span>
                  <span>ERA</span>
                  <span>SO</span>
                  <span>FPPG</span>
                </div>
                <ul className="tp-tbl-pit">
                {rankStat.stat.map((stat, index) => {                   
                  if(index===0){
                    return (                                       
                      <li key={index}>
                        <span>{stat.win}</span>
                        <span>{stat.loss}</span>
                        <span>{stat.pitSv}</span>
                        <span>{stat.pitEra}</span>
                        <span>{stat.pitSo}</span>
                        <span>-</span>                      
                      </li>                                                             
                  )}                 
                })}  
                </ul>
              </div>
            </div>

            <p className="sub-tb-tit">Batters</p>

            <div className="tp-content">
              <div className="awayTeam">
                <div className="tp-tit-pit away">
                  <span className="num">No</span>
                  <span>Name</span>
                  <span className="num">OPS</span>					
                  <span className="num">FPPG</span>
                </div>
                <ul className="tp-tbl-pit">
                <ScrollArea className="playlist" smoothScrolling={true} horizontal={false}>
                {gameData.pl.map((gameData, index) => {                 
                    return ( 
                      gameData.ti===this.state.awayid && (
                        gameData.po === "P" ? null :(
                        <li key={index} className="">                                                                            
                            <span>{gameData.nu}</span>
                            <span>{gameData.nm}</span>
                            <span>{gameData.ops}</span>
                            <span>{gameData.fa} ({gameData.rank})</span>                            
                        </li>
                        )
                      )                    
                   )
                })}
                </ScrollArea>
                </ul>
              </div>
              
              <div className="homeTeam">
                <div className="tp-tit-pit home">
                  <span className="num">No</span>
                  <span>Name</span>
                  <span className="num">OPS</span>					
                  <span className="num ">FPPG</span>
                </div>
                <ul className="tp-tbl-pit">
                <ScrollArea className="playlist" smoothScrolling={true} horizontal={false}>
                {gameData.pl.map((gameData, index) => {                 
                    return ( 
                      gameData.ti===this.state.homeid && (
                        gameData.po === "P" ? null :(
                        <li key={index} className="">                                                                            
                            <span>{gameData.nu}</span>
                            <span>{gameData.nm}</span>
                            <span>{gameData.ops}</span>
                            <span>{gameData.fa} ({gameData.rank})</span>                            
                        </li>
                        )
                      )                    
                   )
                })}
                </ScrollArea>
                </ul>
              </div>
            </div>
            </React.Fragment>
         )}         
      </React.Fragment>
    )
  }
}
export default teamPlayer;
